body{
  font-family: 'Droid Serif', serif;
  font-size: 14px;
  color: #2f2f2f;
  background-color: #f9f7f1;
}
header{
  font-family: 'Playfair Display', serif;
  font-weight: 900;
  font-size: 80px;
  text-transform: uppercase;
  display: inline-block;
  line-height: 72px;
  margin-bottom: 20px;

}
p{
  margin-top: 20px;
  margin-bottom: 20px;
}
.head{
  text-align: center;
  position: relative;

}
.subhead{
  text-transform: uppercase;
  text-align: center;
  position: relative;
  border-bottom: 2px solid #2f2f2f;
  border-top: 2px solid #2f2f2f;
  padding: 12px 0 12px 0;

}
.weatherforcastbox{
  position: relative;
  width: 12%;
  left: 10px;
  border: 3px double #2f2f2f;
  padding: 10px 15px 10px 15px;
  line-height: 20px;
  display: inline-block;
  margin: 0 50px 20px -360px;
}

.columnscontainer{
  column-count: 5;
  column-width: 200px;
  column-rule: solid 1px #2f2f2f;
  column-gap: 3rem;
  font-size: 14px;
  padding: 0 1% 0 1%;
  padding-left: 50px;
  vertical-align: top;
  margin-top: 50px;
  margin-bottom: 50px;
  transition: all .7s;
}

.column{
  line-height: 20px;
  display:contents;
  width: 100%;
  break-after: column;
}

.articlebody{
  break-after: column;
}

.headline{
  text-align: center;
  line-height: normal;
  font-family: 'Playfair Display', serif;
  display: block;
  margin: 0 auto;
}

.headline.hl1{
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
  padding: 10px 0 10px 0;

}

.headline.hl2{
  font-weight: 400;
  font-style: italic;
  font-size: 24px;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}

.headline.hl2:before{
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 7px;
  display: block;
  margin: 0 auto;
}
.headline.hl2:after{
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 13px;
  display: block;
  margin: 0 auto;

}

.headline.hl3{
  font-weight: 400;
  font-style: italic;
  font-size: 36px;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}
.headline.hl4{
  font-weight: 700;
  font-size: 12px;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}
.headline.hl4:before{
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 7px;
  display: block;
  margin: 0 auto;
}
.headline.hl4:after{
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 10px;
  display: block;
  margin: 0 auto;

}

.headline.hl5{
  font-weight: 400;
  font-size: 36px;
  text-transform: uppercase;
  font-style: italic;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}
.headline.hl6{
  font-weight: 400;
  font-size: 18px;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}
.headline.hl6:before{
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 7px;
  display: block;
  margin: 0 auto;
}
.headline.hl6:after{
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 10px;
  display: block;
  margin: 0 auto;

}
.headline.hl7{
  font-weight: 400;
  font-size: 42px;
  box-sizing: inline-block;
  padding: 10px 0 10px 0;
  border: 1px solid #2f2f2f;
  border-radius: 5px;
  max-width: 300px;
}
.headline.hl8{
  font-weight: 500;
  font-size: 30px;
  box-sizing: border-box;
  font-style: oblique;
  padding-top: 40px;
  color:#706868;
}
.headline.hl9{
  font-weight: 400;
  font-size: 18px;
  box-sizing: border-box;
  float: inline-start;
  padding: 10px 0 10px 0;
}

.headline.hl10{
  font-weight: 400;
  font-size: 18px;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
  float: inline-end;
}

.game.p{
  text-align: center;
  position: relative;
}

.collumn .citation{
  font-family: 'Playfair Display', serif;
  font-size: 36px;
  line-height: 44px;
  /*font-style: italic;*/
  text-align: center;
  font-weight: 400;
  display: block;
  margin: 40px 0 40px 0;
  font-feature-settings: "liga", "dlig";

}
.collumn .citation:before{
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 16px;
  display: block;
  margin: 0 auto;
}
.collumn .citation:after{
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 16px;
  display: block;
  margin: 0 auto;
}
.column .figure {
margin: 0 0 20px;
  width: fit-content;
}
.column .figcaption{
font-style: italic;
font-size: 12px;
}
.media {
  -webkit-filter: sepia(80%) contrast(1) opacity(0.8);
  filter: sepia(80%) grayscale(1) contrast(1) opacity(0.8);
  mix-blend-mode: multiply;
  width: 100%;
}
.media_original {
  width: 100%;
}
/*________________________________________________________________________________________________________________________________*/
/*MEDIAQUERIES*/
@media only all and (max-width: 1300px) {
  .weatherforcastbox{
      display: none;
  }

}
@media only all and (max-width: 1200px) {
  .collumn{
      width: 31%;
  }

}
  @media only all and (max-width: 900px) {
  .collumn{
      width: 47%;
  }

}
@media only all and (max-width: 600px) {
  .collumn{
      width: 100%;
  }
.collumn + .collumn {
      border-left: none;
      border-bottom: 1px solid #2f2f2f;
}
  header{
      max-width: 320px;
      font-size: 60px;
      line-height: 54px;
      overflow: hidden;
  }

}